<template>
  <div>
    <div id="playerList">
      <b-btn v-b-modal.modal1 variant="primary" size="sm">添加/修改玩家</b-btn>
       <b-button @click="showModal2" size="sm" variant="danger">
      保存该局结果
    </b-button>
      <!-- Modal Component -->
      <b-modal id="modal1" title="选择玩家" cancel-title="清空" ok-title="确定" @cancel="clearChosenPlayers()" @ok="generateGameInfo()">
        <b-list-group>
          <b-list-group-item v-for="(player,index) in playerList" :key="player.id" class="d-flex justify-content-between align-items-center" @click="selectPlayer(player)">
            {{player.name}}
            <b-badge variant="primary" pill v-if="isPlayerSelected(player)">{{getPlayerNo(player)}}</b-badge>
          </b-list-group-item>
        </b-list-group>
      </b-modal>
       <!-- Modal Component -->
      
    <b-modal ref="myModalRef" hide-footer title="友情提示">
      <div class="d-block text-center">
        <h3>确认保存该结果？</h3>
      </div>
      <b-btn class="mt-3" variant="outline-danger" block @click="hideModal2">100%确认</b-btn>
    </b-modal>
    </div>
    <div id="gameRound">
      <b-list-group>
        <b-list-group-item v-if="chosenPlayers.length>0" v-for="(playerInfo,index) in gameRound.info" :key="playerInfo.playerNo">
          <b-badge class="player-no-badge" variant="primary" pill>{{playerInfo.playerNo}}</b-badge>
          <span class="player-name">{{getPlayerNameById(playerInfo.playerId)}}</span>
          <button class="role-name" @click="switchRole(playerInfo)">
            <i class="fa fa-user-circle" v-if="Number(playerInfo.roleId)<4"></i>
            <i class="fa fa-user-circle-o" v-if="Number(playerInfo.roleId)>3"></i>{{getRoleNameById(playerInfo.roleId)}}</button>
          <button class="player-score" @click="updateScore(playerInfo)">{{playerInfo.score}}</button>
          <span class="victory"> 
            <i class="fa fa-check" v-if="playerInfo.score>0"></i>
            <i class="fa fa-close" v-if="playerInfo.score==0"></i>
            </span>
        </b-list-group-item>
      </b-list-group>
    </div>
  </div>
</template>

<script>
import {saveToLocal,loadGameRoundsFromLocal} from "../common/js/store";
import { EventBus } from "../common/js/eventBus.js";
export default {
  name: 'SingleGame',
  created(){
    this.$http.get('/api/playerList', []).then(function(response){
      this.playerList = response.body.data;
    });
    this.$http.get('/api/roleList', []).then(function(response){
      this.roleList = response.body.data;
    });
  },
  data() {
    return {
      msg: 'Welcome to reports',
      fields: ['name', 'isChosen'],
      gameFields: ['player', 'role', 'score'],
      scoreList:[0,1,1.5],
      chosenPlayers: [],
      roleList: [],

      playerList: [],
      gameRound: {
        date: '',
        info: [
          {
            playerNo: '01',
            playerId: '009',
            roleId: '01',
            score: '1',
            victory: 'false'
          }
        ]
      }
    }
  },
  methods: {
    selectPlayer(player) {
      if (this.chosenPlayers.indexOf(player.id) == -1) {
        this.chosenPlayers.push(player.id)
      } else {
        this.chosenPlayers.splice(
          this.chosenPlayers.findIndex(item => item === player.id),
          1
        )
      }
    },
    isPlayerSelected(player) {
      let playerId = player.id
      if (this.chosenPlayers.indexOf(player.id) == -1) {
        return false
      } else {
        return true
      }
    },
    getPlayerNo(player) {
      let playerNo = this.chosenPlayers.findIndex(item => item === player.id)
      return playerNo + 1
    },
    clearChosenPlayers() {
      this.chosenPlayers = [];
       this.gameRound.info = [];
    },
    generateGameInfo() {
      let info = this.chosenPlayers.map(function(playerId, index) {
        return {
          playerNo: index + 1,
          playerId: playerId,
          roleId: '01',
          score: 0,
          victory: false
        }
      })
      this.gameRound.info = info
    },
    getPlayerNameById(playerId) {
      return this.playerList[
        this.playerList.findIndex(item => item.id === playerId)
      ]['name']
    },
    getRoleNameById(roleId) {
      return this.roleList.find(item => item.roleId === roleId)['name']
    },
    switchRole(playerInfo){
      let roleId = playerInfo.roleId;
      let playerNo = playerInfo.playerNo;
      if(Number(roleId)<8){
        roleId= '0'+ (Number(roleId)+1);
      }else{
        roleId='01';
      }
      this.gameRound.info[Number(playerNo)-1]['roleId']=roleId;
    },
    updateScore(playerInfo){
      let score = playerInfo.score;
      let scoreIndex = this.scoreList.findIndex(item => item == score);
      let playerNo = playerInfo.playerNo;
      if(Number(score)<1.5){
        score = this.scoreList[scoreIndex+1]
      }else{
        score = this.scoreList[0]
      }
       this.gameRound.info[Number(playerNo)-1]['score']=score;
    },
    saveGameRound(){
      let dateObj = new Date();
      let date= dateObj.getDate();
      let month = dateObj.getMonth()+1;
      if(String(month).length<2){
        month = "0"+month;
      }
      let year = dateObj.getFullYear();
      let gameDate = ""+year+month+date;
      this.gameRound.date= gameDate;
      saveToLocal(this.gameRound);
        EventBus.$emit('updateRank')
    },
     showModal2 () {
      this.$refs.myModalRef.show()
    },
    hideModal2 () {
      this.$refs.myModalRef.hide();
      this.saveGameRound();
    }
  }
}
</script>

  
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}

#playerList {
  margin-top: 20px;
}
#gameRound .player-no-badge {
  position: relative;
  top: -2px;
  margin-right: 10px;
}
#gameRound .list-group-item {
  text-align: left;
  position: relative;
}
#gameRound button {
  margin: 0;
  padding: 0;
  border: 1px solid transparent;
  outline: none;
  background-color: white;
  cursor:pointer;
}
.player-name {
  margin-right: 20px;
}
.fa-user-circle {
  color: green;
  margin-right: 2px;
}
.fa-user-circle-o {
  color: red;
  margin-right: 2px;
}
.role-name {
  position: absolute;
  left: 38%;
}
.player-score {
  position: absolute;
  left: 75%;
}
.victory {
  position: absolute;
  left: 85%;
}
.fa-check{
  color:green;
}
.fa-close{
  color:red;
}
</style>




