//保存数据
export function saveToLocal(gameRound) {
	if (!window.localStorage.hasOwnProperty("gameRounds")) {
		window.localStorage.setItem("gameRounds", "[]");
	}
	let gameRounds = JSON.parse(window.localStorage.getItem("gameRounds"));
	gameRounds.push(gameRound);
	window.localStorage.setItem("gameRounds",JSON.stringify(gameRounds));
};
//读取数据
export function loadGameRoundsFromLocal() {
	if(!window.localStorage.hasOwnProperty("gameRounds")){
		return [];
	}else{
		return JSON.parse(window.localStorage.getItem("gameRounds"));
	}
};



