<template>
<div id="rankList">
  <h4>英雄榜</h4>
   <b-list-group>
        <b-list-group-item v-if="rankList.length>0" v-for="(playerInfo,index) in rankList" :key="playerInfo.id" >
          <b-badge class="player-no-badge"  :variant="getBtnVariant(index)" pill>{{index+1}}</b-badge>
          <span class="player-name">{{playerInfo.name}}</span>
          <span class="score">{{playerInfo.score}}</span>
        </b-list-group-item>
      </b-list-group>
</div>
</template>

<script>

import { EventBus } from "../common/js/eventBus.js";
import {saveToLocal,loadGameRoundsFromLocal} from "../common/js/store";
export default {
  name: 'Rank',
 created(){
    this.$http.get('/api/playerList', []).then(function(response){
      this.playerList = response.body.data;
      this.addScoreAttr();
      this.getRankList();
    });
  
    
  },
  mounted(){
EventBus.$on('updateRank', () => {
        this.getRankList();
      })
  },
  data () {
    return {
      rankList: [],
      playerList:[]
    }
  },
  methods:{
    getPlayerNameById(playerId) {
      return this.rankList[
        this.rankList.findIndex(item => item.id === playerId)
      ]['name']
    },
    getBtnVariant(indexNo){
      if(indexNo<3){
        return 'danger';
      }else{
        return 'primary';
      }
    },
    getRankList(){
      let gameRounds = loadGameRoundsFromLocal();
      let scoreList = [];
      for(let gameRound of gameRounds){
        let gameInfo = gameRound.info;
        for(let playerRoundInfo of gameInfo){
         let playerRoundInfoId = playerRoundInfo.playerId;
     
         for(let player of this.playerList){
           if(player.id == playerRoundInfoId){
              player['score'] = Number(player['score'])+Number(playerRoundInfo.score);
           }
         }
        
        }
      }
      this.sortRankList(this.playerList);      
    },
    addScoreAttr(){
        for(let player of this.playerList){
           if(!player.hasOwnProperty('score')){
                player['score'] = 0;
             }
        }
    },
    sortRankList(arr){

let compare = function(obj1,obj2){
    var val1 = obj1.score;
    var val2 = obj2.score;
    if(val1 < val2){
       return 1;
    }else if(val1 > val2){
       return -1;
    }else{
       return 0;
    }
}
let sortArr = arr.sort(compare);
this.rankList = this.playerList;
    }
  }
}
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}

#rankList {
  padding-top: 20px;
  background-color:#000;
  color:#fff;
}
#rankList .player-no-badge {
  position: relative;
  top: -2px;
  margin-right: 10px;
    width: 28px;
}
#rankList .list-group-item {
  text-align: left;
  position: relative;
   background-color:#fff;
  color:#000;
}
.score {
  position: absolute;
  left: 85%;
}
.topranker{
  background-color:#000;
  color:#fff;
}

</style>



