<template>
  <div class="hello">
    <b-card no-body>
      <b-tabs card>
        <b-tab no-body title="单日局" active>
          <single-game></single-game>
        </b-tab>
        <b-tab no-body title="积分排名">
          <rank></rank>
        </b-tab>
        <b-tab title="报告">
          <reports></reports>
        </b-tab>

      </b-tabs>
    </b-card>
  </div>
</template>

<script>
import SingleGame from './SingleGame'
import Rank from './Rank'
import Reports from './Reports'

export default {
  name: 'HelloWorld',
  data() {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  components: {
    SingleGame,
    Rank,
    Reports
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #000;
}
</style>



